if ($(window).width() <= 768) {
    $('#youtuber-box').removeClass('swiper--pagination-hidden');
}

else {
    $('#youtuber-box').addClass('swiper--pagination-hidden');
}

$(window).resize(function () {
    if ($(window).width() <= 768) {     
        $('#youtuber-box').removeClass('swiper--pagination-hidden');
    }
    
    else {
        $('#youtuber-box').addClass('swiper--pagination-hidden');
    }
});

var YoutuberBox = new Swiper('#youtuber-box .swiper-container', {
    slidesPerView: 6,
    slidesPerGroup: 6,
    spaceBetween: 15,
    autoplay: {
        delay: 4500,
        disableOnInteraction: false
    },

    pagination: {
        el: '#youtuber-box .swiper-pagination',
        clickable: true
    },

    navigation: {
        nextEl: '#youtuber-box .swiper-button-next',
        prevEl: '#youtuber-box .swiper-button-prev',
    },

    breakpoints: {
        480: {
            slidesPerView: 2,
            slidesPerGroup: 2,
        },
        736: {
            slidesPerView: 3,
            slidesPerGroup: 3,
        },
        1024: {
            slidesPerView: 4,
            slidesPerGroup: 4,
        },
    }
});