if ($(window).width() <= 768) {
    $('#shop-box').removeClass('swiper--pagination-hidden');
}

else {
    $('#shop-box').addClass('swiper--pagination-hidden');
}

$(window).resize(function () {
    if ($(window).width() <= 768) {     
        $('#shop-box').removeClass('swiper--pagination-hidden');
    }
    
    else {
        $('#shop-box').addClass('swiper--pagination-hidden');
    }
});

var ShopBox = new Swiper('#shop-box .swiper-container', {
    slidesPerView: 6,
    slidesPerGroup: 6,
    spaceBetween: 15,
    autoplay: {
        delay: 4700,
        disableOnInteraction: false,
    },

    pagination: {
        el: '#shop-box .swiper-pagination',
        clickable: true
    },

    navigation: {
        nextEl: '#shop-box .swiper-button-next',
        prevEl: '#shop-box .swiper-button-prev'
    },

    breakpoints: {
        480: {
            slidesPerView: 2,
            slidesPerGroup: 2,
        },
        736: {
            slidesPerView: 3,
            slidesPerGroup: 3,
        },
        1024: {
            slidesPerView: 4,
            slidesPerGroup: 4,
        },
    }
});